import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import { useAlert } from "react-alert";
import { useQuery } from "@tanstack/react-query";
import { addReply, viewHelpdata } from "../../apiServices/home/homeHttpService";
import { showAlert } from "../commonComponent/alertManager";
import { useForm } from "react-hook-form";
import moment from "moment";

function HelpDetails() {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    mode: "onChange",
  });
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();
  const alert = useAlert();
  const navigate = useNavigate();

  const { data: details } = useQuery({
    queryKey: ["helpDetail", id],
    queryFn: async () => {
      return viewHelpdata(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.help,
  });
  const onSubmit = async (data) => {
    const response = await addReply(data, id);
    if (!response.error) {
      showAlert(alert, response.message, { timeout: 3000 });
      navigate(`/admin/help-&-Support?page=${queryParams.get("page") || 1}`);
      reset();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link
            to={`/admin/help-&-Support?page=${queryParams.get("page") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div className="d-flex gap-4">
                    <div className="dash-review-user-img">
                      <img
                        src={
                          details?.category === "user" &&
                          details?.users[0]?.profile
                            ? details?.users[0]?.profile
                            : details?.category === "merchant" &&
                              details?.merchants[0]?.profile
                            ? details?.merchants[0]?.profile
                            : "../../assets/img/user1.png"
                        }
                        alt=""
                        className="w-100 h-100"
                      />
                    </div>
                    <div className="w-100">
                      <p
                        className="fs-6 fw-bold mb-0"
                        style={{ textTransform: "capitalize" }}
                      >
                        {details?.name}
                      </p>
                      <small>
                        {" "}
                        {moment(details?.createdAt).format(
                          "DD MMM YYYY, hh:mm A"
                        )}
                      </small>
                      <div className="mt-3">
                        <p className="fs-6 fw-light">{details?.notes}</p>
                      </div>
                      <form
                        className="row mt-4"
                        onSubmit={handleSubmit(onSubmit)}
                      >
                        <div className="col-md-6">
                          <div className="form-design">
                            <div className="form-group">
                              <label htmlFor="form-label">Update Status</label>
                              <select
                                name="status"
                                id="status"
                                {...register("status", {
                                  required: true,
                                })}
                                className="form-select"
                              >
                                <option value="" disabled selected>
                                  Select a status
                                </option>
                                <option value="In Progress">In Progress</option>
                                <option value="Solved">Solved</option>
                                <option value="Pending">Pending</option>
                              </select>
                              {errors.status &&
                                errors.status.type === "required" && (
                                  <p className="form-error">
                                    This field is required
                                  </p>
                                )}
                            </div>
                          </div>
                        </div>
                        <div className="col-md-6">
                          <div className="form-design">
                            <div className="form-group">
                              <label htmlFor="form-label">Add Reply</label>
                              <textarea
                                row={4}
                                className="form-control"
                                name="reply"
                                id="reply"
                                {...register("reply", {
                                  required: true,
                                })}
                              />
                              {errors.reply &&
                                errors.reply.type === "required" && (
                                  <p className="form-error">
                                    This field is required
                                  </p>
                                )}
                            </div>
                          </div>
                        </div>
                        <div className="mt-3">
                          <button type="submit" className="comman_btn w-fit">
                            {" "}
                            update
                          </button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default HelpDetails;
